import { RouteRecordRaw } from "vue-router";

import InternalPage from "@/layouts/InternalPage.vue";
import Children from "@/views/internal/Children.vue";

export const AdminRoutes: Array<RouteRecordRaw> = [
  {
    path: "/admin/users/:operation?/:id?",
    name: "admin/users",
    component: () =>
      import(/* webpackChunkName: "admin" */ "@/views/internal/Users.vue"),
    props: true,

    meta: {
      title: "Users",
      requiresAuth: true,
      hasEveryPermissions: ["user:read"],
      layout: InternalPage,
    },
  },

  {
    path: "/children",
    name: "children",
    component: Children,

    meta: {
      title: "My Family",
      requiresAuth: true,
      hasAnyPermissions: ["user:impersonate", "user:impersonate:parent"],
      layout: InternalPage,
    },
  },

  {
    path: "/admin/roles",
    name: "admin/roles",
    component: () =>
      import(/* webpackChunkName: "admin" */ "@/views/internal/Roles.vue"),

    meta: {
      title: "Roles",
      requiresAuth: true,
      hasEveryPermissions: ["role:read"],
      layout: InternalPage,
    },
  },
];
