import { Module, GetterTree, ActionTree, MutationTree } from "vuex";

import { getClient, destroyClient } from "@/util/splitio";
import type { RootState } from "./index";

export type Treatments = Record<string, string>;

export type TreatmentsState = {
  treatments: Treatments;
  ready: boolean;
};

export type FeatureToggle = (name: string) => boolean;

export const TreatmentsDefaults: Treatments = {
  sso_new_layout: "off",
  // sso_code_via_sms: "off",
  "USER-381-not-allow-to-edit-child-email": "off",
  "USER-392-separate-profile-forms": "off",
  "USER-396-prefill-sign-in-email-from-query": "on",
  "USER-396-show-alert-for-raise-users": "on",
  "USER-416-new-reset-password-flow": "on",
  "USER-420-disable-password-editing-for-internal-users": "off",
  "USER-421-move-update-email-form-down": "off",
  // "USER-428-allow-transport-choose-for-signup-code-resend": "on",
  "USER-429-disables-update-button-without-field-change": "on",
  "USER-434-allow-impersonation-without-user-edit-permission": "off",
  "USER-445-not-allow-to-edit-internal-email": "off",
  "USER-455-sync-users-query-with-url": "off",
  "USER-461-use-full-story-custom-events": "off",
  "USER-466-redirect-to-azure-on-internal-password-reset": "off",
};

const state: TreatmentsState = {
  treatments: TreatmentsDefaults,
  ready: false,
};

// HINT: In component should use:
// async beforeMount() {
//   await this.$store.dispatch("useTreatments");
// }

const getters: GetterTree<TreatmentsState, RootState> = {
  treatments: (state) => state.treatments,
  treatment:
    (state) =>
    (name: string): string =>
      state.treatments[name],
  featureOn: (state) => (name: string) => state.treatments[name] === "on",
  featureOff: (state) => (name: string) => state.treatments[name] !== "on",

  isTreatmentsReady: (state) => state.ready,
};

const mutations: MutationTree<TreatmentsState> = {
  setTreatments(state, treatments) {
    Object.assign(state.treatments, treatments);
    state.ready = true;
  },

  clearTreatments(state) {
    Object.keys(TreatmentsDefaults).forEach(
      (name) => delete state.treatments[name]
    );
    state.ready = false;
  },
};

const actions: ActionTree<TreatmentsState, RootState> = {
  async useTreatments({ dispatch, getters }) {
    const client = await getClient(getters.me.id);
    await client.ready();

    if (!getters.isTreatmentsReady) {
      dispatch("fetchTreatments", client);

      client.on(client.Event.SDK_UPDATE, async () => {
        dispatch("fetchTreatments", client);
      });
    }
  },

  async fetchTreatments({ commit, getters }, client) {
    if (client) {
      const attributes = {
        users: getters.isAuthorized ? [getters.me.id] : [],
        permissions: getters.me.permissions || [],
      };

      commit(
        "setTreatments",
        await client.getTreatments(Object.keys(TreatmentsDefaults), attributes)
      );
    }
  },

  async destroyTreatments({ commit }) {
    await destroyClient();
    commit("clearTreatments");
  },
};

export const treatments: Module<TreatmentsState, RootState> = {
  state,
  getters,
  mutations,
  actions,
};
