
import { Options } from "vue-class-component";

import Field from "@/core/Field";

import RadioButton from "@/components/snap-ui/RadioButton.vue";

const LANGUAGES: Record<string, string> = {
  en: "English",
  es: "Spanish",
};

@Options<LanguageSelect>({
  props: ["label"],
  components: { RadioButton },

  watch: {
    modelValue(value: string) {
      (this as unknown as LanguageSelect).language = value;
    },
  },
})
export default class LanguageSelect extends Field {
  label?: string;
  language?: string;

  beforeMount() {
    this.language = this.modelValue;
  }

  get languages(): Record<string, string> {
    return LANGUAGES;
  }
}
