<template>
  <div class="spinner"></div>
</template>

<style scoped lang="scss">
.spinner {
  display: block;
}

.spinner::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  border-width: 2px;
  border-style: solid;
  border-color: transparent #7a7a7a #7a7a7a;
  animation: spinAround 500ms infinite linear;
}

@keyframes spinAround {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
</style>
