import { Components } from "@snap-mobile/snap-ui/dist/types/components";

let FS: Components.SnapFullstory | undefined;
const eventsQuery: Array<{ eventName: string; eventProperties: object }> = [];

export const initialize = (fs: unknown): void => {
  (fs as Element)?.addEventListener<any>(
    "snap-fullstory-ready",
    (e: CustomEvent) => {
      FS = e.detail;

      if (FS) {
        eventsQuery.forEach(({ eventName, eventProperties }) => {
          event(eventName, eventProperties);
        });
      }
    }
  );
};

export const event = (
  eventName: string,
  eventProperties: object = {}
): void => {
  if (FS) {
    FS.event(eventName, eventProperties);
  } else {
    eventsQuery.push({ eventName, eventProperties });
  }
};
