import { Module, GetterTree, ActionTree, MutationTree } from "vuex";

import type { User, UserWithPermissions } from "@/types/graphql";
import type { RootState } from "./index";

import {
  login,
  logout,
  me,
  userImpersonate,
  userImpersonateLogout,
  userRefreshSession,
  userSignupConfirm,
  userUpdate,
} from "@/services/user.service";

type UserState = {
  user?: UserWithPermissions;
};

const state: UserState = {
  user: {},
};

export type SinglePermissionChecker = (permission: string) => boolean;
export type MultiplePermissionsChecker = (permission: Array<string>) => boolean;

const getters: GetterTree<UserState, RootState> = {
  me: (state) => state.user,
  isAuthorized: (state) => state.user?.id !== undefined,
  isSpoofed: (state) =>
    state.user?.parent !== undefined && state.user?.parent !== null,

  hasPermission: (state) => (permission: string) =>
    state.user?.permissions?.includes(permission),

  hasEveryPermissions: (state) => (permissions: Array<string>) =>
    permissions.every((permission) =>
      state.user?.permissions?.includes(permission)
    ),

  hasAnyPermissions: (state) => (permissions: Array<string>) =>
    permissions.some((permission) =>
      state.user?.permissions?.includes(permission)
    ),
};

const mutations: MutationTree<UserState> = {
  updateUserState(state, user: UserWithPermissions) {
    state.user = { ...(state.user || {}), ...user };

    if (state.user.profilePicture) {
      state.user.profilePicture += `?${+new Date()}`;
    }

    window.dispatchEvent(
      new CustomEvent("SNAP_AUTH_UPDATED", {
        detail: "setAuthData",
      })
    );
  },

  clearUserState(state) {
    state.user = {};

    window.dispatchEvent(
      new CustomEvent("SNAP_AUTH_UPDATED", {
        detail: "authFailed",
      })
    );
  },
};

const actions: ActionTree<UserState, RootState> = {
  async me({ commit, state }) {
    const user = await me();

    if (state.user?.id !== user?.id) {
      commit("updateUserState", user);
    }
  },

  async login({ dispatch }, { email, password, consumer }) {
    await login(email, password, consumer);

    await dispatch("me");

    await dispatch("destroyTreatments");
    await dispatch("useTreatments");
  },

  async logout({ commit, dispatch }) {
    await logout();

    commit("clearUserState");
    commit("clearConsumerData");

    await dispatch("destroyTreatments");
    await dispatch("useTreatments");
  },

  async userUpdate({ commit }, user: User) {
    commit("updateUserState", await userUpdate(user));
  },

  async userRefreshSession() {
    await userRefreshSession();
  },

  async userSignupConfirm({ dispatch }, { email, code }) {
    await userSignupConfirm(email, code);
    await dispatch("me");
  },

  async userImpersonate({ dispatch }, userId) {
    await userImpersonate(userId);
    await dispatch("me");
  },

  async userImpersonateLogout({ dispatch }) {
    try {
      await userImpersonateLogout();
      await dispatch("me");
    } catch {
      await dispatch("logout");
    }
  },
};

export const user: Module<UserState, RootState> = {
  state,
  getters,
  mutations,
  actions,
};
